img {
  height: 300px;
  padding-bottom: 5px;
  border-color: #76885b5c;
  border: 4px solid #76885b;
  background-color: #76885b;
}
body {
  height: 100vh;
  background-image: linear-gradient(#76885b8a, rgba(0, 0, 0, 0.627)),
    url("/src/components/Data/backgroundpic/book-1822474_1920.jpg");
  background-size: cover;
  background-position: center;
  z-index: -1;
}
/* #76885b5c  tourl('/src/components/Data/SpeakersPics/Designer\ \(1\).jpeg'); */
.textarea {
  color: #000000;
  margin-top: 5px;
  margin-bottom: 10px;
  padding-top: 5px;
  padding-bottom: 5px;
  background: linear-gradient(to right bottom, #eeeeee50, #76885b29);
  background-color: #76885b5c;
  border-color: #000000;
  box-shadow: 3px 2px 5px #00000055;
}
.flex {
  display: flex;
  gap: 5px;
  justify-content: space-between;
}
.form {
  padding-left: 50px;
}
.label {
  color: white;
}
.speaker {
  padding-right: 50px;
  display: flex;
  flex-direction: column;
  text-align: center;
  flex-wrap: wrap;
}
.roboto-slab-iq {
  font-family: "Roboto Slab", serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

.audio {
  margin-top: 15px;
}
.btn {
  border-radius: 5%;
  background: linear-gradient(45deg, #eeeeee, #76885b5c);
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 10px;
  padding-right: 10px;
  box-shadow: 3px 2px 5px #66636355;
  color: #000000;
  margin-bottom: 5px;
  border-color: rgb(91, 93, 95);
}
.rules {
  box-shadow: 3px 2px 5px #66636355;
  border-color: rgb(91, 93, 95);
  background: linear-gradient(45deg, #eeeeee, #76885b5c);
  height: 5px;
  width: 5px;
  padding: 4px;
  text-align: center;
  margin-left: 10px;
}
.rule {
  margin-left: 2px;
  padding-left: 7px;
  color: #000000;
}
.link {
  text-decoration: line-through;
  color: rgb(0, 0, 0);
}
.rules {
  height: 40px;
  width: 80px;
}
.foot {
  position: absolute;
  height: 20px;
  text-align: center;
  bottom: 0;
  width: 100%;
  background-color: #627254;
}

@media (max-width: 500px) {
  .rule {
    margin-left: 2px;
    padding-left: 7px;
    color: #eeeeee;
  }
  .flex {
    flex-direction: column;
  }
  body {
    /* height: 800px; */
    height: max-content;
  }
  .form {
    padding-left: 5px;
  }

  .audio {
    margin-top: 15px;
    margin-bottom: 20px;
    /* position: absolute; */
  }
  .div {
    flex-direction: column;
    gap: 3px;
  }
  .speaker {
    padding-right: 5px;
    display: flex;
    flex-direction: column;
    text-align: center;
    flex-wrap: wrap;
  }
  .foot {
    height: 40px;
    position: relative;
    text-align: center;
    width: 100%;
    background-color: #326a00;
  }

  h2 {
    color: #ffffff;
  }
}
@media (max-width: 300px) {
  .foot {
    height: 60px;
  }
  .textarea{
    color: white;
    background-color: #62725459
  }

}
